.food-sign-container{
    border: rgb(2, 124, 18) 1px solid;
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    padding: 2px;
}

.food-sign-circle{
    width: 7px;
    height: 7px;
    background-color: rgb(2, 124, 18);
    border-radius: 50%;
}